import styles from "./order-details.module.css";

import doneImage from "../../../images/done.svg";

const OrderDetails = ({ orderNumber }) => {
	return (
		<div className={styles.container}>
			<p className="text text_type_digits-large pb-4">{orderNumber}</p>
			<p className="text text_type_main-medium"> </p>
			<img src={doneImage} alt="  " className="pt-15 pb-15"></img>
			<p className="text text_type_main-default">   </p>
			<p className="text text_type_main-default text_color_inactive pt-2">    </p>
		</div>
	);
};

export default OrderDetails;